@import '../../../styles/utils/variables', '../../../styles/utils/mixins';

.editButton {
  svg {
    width: 16px;
    height: 16px;
  }
}

.data_container {
  border-radius: 4px;
  border: 1px solid $base-dark-gray;
  background: white;
  padding: 8px 16px;
  padding-right: 20px;
  margin-top: 16px;

  & > h3 {
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    padding-block: 8px;
  }
}

input:disabled {
  background-color: white;
}

.title {
  display: flex;
  flex-direction: column;
}

.subtypeName {
  font-size: 17px;

  @include respond-to('medium') {
    font-size: 12px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.files {
  font-size: 14px;
  padding-block: 8px;

  & > div {
    margin-top: 0;
  }
}

.buttons {
  display: flex;
  justify-content: space-between;
  padding-block: 8px;
}

.button {
  font-size: 14px !important;
}

.input {
  font-size: 14px !important;
}
