@import 'utils/variables', 'utils/mixins';

.container {
  margin-top: 16px;
}

.field {
  margin-top: 8px;

  display: flex;
  flex-direction: row;
  align-items: center;

  width: 100%;
  border: 1px dashed $base-dark-gray;
  padding: 24px;
  gap: 16px;

  border-radius: 4px;

  &.active {
    background-color: #ebfff8;
    border-color: #00ed9c;
  }
}

.title {
  font-size: 14px;
  font-weight: 500;
}

.list {
  display: flex;
  flex-flow: column;
}

.file {
  cursor: pointer;
  margin-top: 8px;
  padding: 8px 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-radius: 4px;
  background-color: $light-gray;
  box-sizing: border-box;
  min-height: 40px;

  &__title {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  &__con {
    width: 16px;
    height: 16px;
    min-width: 16px;
    min-height: 16px;
  }

  &__delete {
    margin-left: 16px;
    width: 12px;
    height: 12px;
    min-width: 12px;
    min-height: 12px;

    cursor: pointer;
  }

  @include darkTheme {
    color: $white;
    background: $black-blue;
  }
}

.error {
  color: $red;
  font-size: 14px;
  line-height: 20px;
  display: flex;
  margin-top: 12px;

  &__icon {
    margin-right: 12px;
    min-width: 14px;
    position: relative;
    top: 1px;
  }
}
