@import 'utils/variables', 'utils/mixins';

.wrapper {
  &.top {
    display: flex;
    flex-direction: column;

    .label {
      margin-bottom: 8px;
    }
  }

  &.left {
    .label {
      margin-right: 12px;
    }
  }
}

.textarea {
  outline: none;
  box-sizing: border-box;

  border: 1px solid $base-dark-gray;
  border-radius: 4px;

  height: 64px;
  padding: 8px 12px;

  font-size: 16px;
  line-height: 24px;

  &:not(:disabled) {
    &:focus,
    &:hover {
      border: 1px solid $base-dark-gray;
    }
  }

  &:disabled {
    background: $base-gray;
    color: $base-black;
  }

  @include darkTheme {
    background: $black-blue;
    color: $white;
    border-color: $grey;
  }
}
