@import
'../../../styles/utils/variables',
'../../../styles/utils/mixins';

.container_validation {
  font-weight: 100;
  color: #FF6161;
  font-size: 17px;
  padding-top: 10px;

  display: flex;
  flex-direction: row;
  align-items: flex-start;

  @include respond-to('large') {
    font-size: 12px;
  }
}

.error_icon {
  margin-right: 10px;
  margin-top: 3px;

  min-width: 14px;
  min-height: 14px;
  max-height: 14px;
  max-width: 14px;

  @include respond-to('large') {
    margin-top: -1px;
    width: 10px;
  }
}
